.about_me body {
    align-items: center;
    background-color: #f2f2f2;
    display: flex;
    font-family: "Merriweather", serif;
    flex-wrap: wrap;
    justify-content: center;
    height: 100vh;
    margin: 0;
  }
  .about_me.person {
    align-items: center;
    display: flex;
    flex-direction: column;
    width: 280px;
  }
  .about_me .container {
    border-radius: 50%;
    height: 312px;
    -webkit-tap-highlight-color: transparent;
    transform: scale(0.48);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    width: 400px;
  }
  .about_me .container:after {
    background-color: #f2f2f200;
    content: "";
    height: 10px;
    position: absolute;
    top: 390px;
    width: 100%;
  }
  .about_me .container:hover {
    transform: scale(0.54);
  }
  .about_me   .container-inner {
    clip-path: path(
      "M 390,400 C 390,504.9341 304.9341,590 200,590 95.065898,590 10,504.9341 10,400 V 10 H 200 390 Z"
    );
    position: relative;
    transform-origin: 50%;
    top: -200px;
  }
  .about_me .circle {
    background-color: #fee7d3;
    border-radius: 50%;
    cursor: pointer;
    height: 380px;
    left: 10px;
    pointer-events: none;
    position: absolute;
    top: 210px;
    width: 380px;
  }
  .about_me .img {
    pointer-events: none;
    position: relative;
    transform: translateY(20px) scale(1.15);
    transform-origin: 50% bottom;
    transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  .about_me .container:hover .img {
    transform: translateY(0) scale(1.2);
  }
  .about_me .img1 {
    left: 22px;
    top: 164px;
    width: 340px;
  }
 
